<script>
	export default {
		data() {
			return {
				ishow: false,
				is_lia_show:false,
				screenWidth: document.documentElement.clientHeight,
				content: '',
				xz:[
					{
						title:'第一球',
						titlex:['1','2']
					}
				],
				tists_arr:[],
				dsz:0,
				xzje:0
			}
		},
		props: {
			endTime: {
				type: String,
				default: '1515400000'
			},
			endText: {
				type: String,
				default: '00 : 00 : 00'
			}
		},
		methods: {
			change1(tab){
				if(parseInt(tab) == 1)
				{
					this.is_lia_show = false;
					this.tists_arr.length = 0;
					$('#j-all tr').css('background','rgba(0, 0, 0, 0)');
					this.dsz = 0;

				}else if(parseInt(tab) == 2)
				{
					this.is_lia_show = true;
				}
			},
			countdowm(timestamp) {
				let self = this;
				let timer = setInterval(function() {
					let nowTime = new Date();
					let endTime = new Date(timestamp * 1000);
					let t = endTime.getTime() - nowTime.getTime();
					if(t > 0) {
						let day = Math.floor(t / 86400000);
						let hour = Math.floor((t / 3600000) % 24);
						let min = Math.floor((t / 60000) % 60);
						let sec = Math.floor((t / 1000) % 60);
						hour = hour < 10 ? "0" + hour : hour;
						min = min < 10 ? "0" + min : min;
						sec = sec < 10 ? "0" + sec : sec;
						let format = '';
						if(day > 0) {
							format = `${day} : ${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour > 0) {
							format = `${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour <= 0) {
							format = `${min} : ${sec}`;
						}
						self.content = format;
					} else {
						clearInterval(timer);
						self.content = self.endText;
					}
				}, 1000);
			}
		},
		mounted: function() {
			let that = this;
			window.onresize = function() {
				that.screenWidth = document.documentElement.clientHeight;
			}
			$('#j-all tr').click(function(){
					 var xiaodans = {};	 
					 if($(this).find('.icon_2').html() == undefined){
					 	xiaodans['jis']=$(this).find('.td_left_one').find('span').html()
					 }else{
					 	xiaodans['jis'] = $(this).find('.icon_2').html()
					 }	
					if($(this).find('.td_left_tows').find('span').html() == undefined){
						 xiaodans['zhi']=$(this).find('span').html()
					}else{
						 xiaodans['zhi']=$(this).find('.td_left_tows').find('span').html()
					}
					 xiaodans['zhis']=$(this).parent().find('th').html()
					 if($(this).css('background-color') == 'rgba(0, 0, 0, 0)'){
					 	 $(this).css('background','rgb(255,245,178)');
					 	that.dsz++
					 }else{
					 	 $(this).css('background','rgba(0, 0, 0, 0)');
					 	that.dsz--
					 }
					 that.tists_arr.push(xiaodans)
			})
			
			
			$('#reset').click(function(){
				$('#j-all tr').css('background','rgba(0, 0, 0, 0)');
				 that.tists_arr = [];
				 that.dsz = '0'
			})
			
			$('#xiazhu').click(function(){
				that.xzje = $('#jines').val();
				if($('#jines').val() == ''){
					alert('请填写下注金额')
					return false;
				}else{
					$('#xiaz_tis').show();
				}
			})
			$('#quxiao_tis').click(function(){
				$('#xiaz_tis').hide();
			})
			this.countdowm(this.endTime)
		}

	}
</script>
